/*Header*/
header{
    background-color: rgb(0, 16, 33);
    width: 100%;
    min-width: 900px;
    height: auto;
    display: flex;
    justify-content: center;    
}

.h-content{
    width: 90%;
    display: flex;
    justify-content: flex-start;
}

header img{
    width: 150px;
}
h1{ 
    color: white; 
    margin-left: 20px;
}

*{
    height: auto;    
}

.r-d-user, .r-d-pass, .r-d-desciption, .r-d-enable, .r-d-delete{
    width: 20%;
    text-align: left;
    font-size: 20px;
    padding: 5px 0px 5px 10px;
    line-height: 30px;
}

.r-d-enable, .r-d-delete{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
}

.btnDelete{
    width: 70px;
    font-size: 15px;
    padding: 5px;   
    background-color: rgba(0, 16, 33, 0.9);
    color:#ffffff;
    border: none; 
    border-radius: 5px; 
    cursor: pointer;
    margin: auto;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5) inset;
}

.btnDelete:hover {
    background-color: rgba(0, 16, 33, 0.7);
}

/* nieto form */
.b-form{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    padding: 20px 0px;
}

.b-form div h2{
    margin: 0px 0px 10px 0px;
}

.form-addNieto{
    width: 100%;  
    display: flex;
    text-align: center;
}

input, textarea{
      margin-right: 20px;
      height: 30px;
      border-Radius: 5px;
      box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 1) inset;
}

textarea{
    width: 300px;
    height: 30px;
    resize: none;
    border: solid 1px black;
}

.btnEnviar{
    font-size: 15px;
    font-weight: bold;
    height: 35px;
    width: 100px;
    background-color:  rgba(0, 16, 33, 0.9);
    color: #ffffff;
    border: none;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5) inset;
    cursor: pointer;
}

.btnEnviar:hover{
    background-color: rgba(0, 16, 33, 0.7);
}
